import React, { useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Card, CardBody, CardTitle, Col, CustomInput, FormGroup, Row } from 'reactstrap';
import {
  ACTION_EVENTS,
  alertManager,
  AlertProps,
  AlertType,
  EditForm,
  EditViewProps,
  ERROR,
  getSelectedItem,
  InputGroupInline,
  InputTooltipProps,
  showToast,
  WindowComponent,
  withTooltip,
} from 'summer';
import { CLASS_NAME, Perfil } from '../../../../models/dtos/ger/perfil';
import { atualizarGlobalParameter } from '../../../../services/ger.service';
import { Reducers } from '../../../../store/ducks';
import { GlobalState, setGlobalParameter } from '../../../../store/ducks/global.duck';

interface PerfilEditViewProps extends EditViewProps<Perfil> {}

const PerfilEditView: React.FC<PerfilEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<Perfil>({ defaultValues: selectedItem, mode: 'onBlur' });
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const dispatch = useDispatch();

  const [podeAlterarAcessoFinanceiro, setPodeAlterarAcessoFinanceiro] = useState(false);
  const [possuiConnect, setPossuiConnect] = useState(false);
  const [possuiBox, setPossuiBox] = useState(false);
  const [possuiSite, setPossuiSite] = useState(false);

  const propToolTip = (messagem: string): InputTooltipProps => {
    return {
      message: messagem,
      placement: 'top-start',
    };
  };

  useEffect(
    () => {
      const { perfil, acesso } = globalReducer.globalParameter;
      const { calimaConnect, calimaBox, calimaSite } = acesso;

      setPossuiConnect(!calimaConnect);
      setPossuiBox(!calimaBox);
      setPossuiSite(!calimaSite);

      if (perfil) {
        setPodeAlterarAcessoFinanceiro(perfil.id !== 1);
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const afterPersist = () => {
    atualizarGlobalParameter(globalReducer.globalParameter.usuario, {
      errorFunction: mensagem => {
        showToast(mensagem, ERROR);
      },
      thenFunction: globalParameter => {
        dispatch(setGlobalParameter(globalParameter));
      },
    });
  };

  const beforePersist = (formData: Perfil, done: any) => {
    const { contabil, fiscal, controlePatrimonial, protocolo, honorario, folhaPagamento, connect, box, site } = formData.perfilModulo;

    const usaCalima: boolean = contabil || fiscal || controlePatrimonial || protocolo || honorario || folhaPagamento;
    const usaExternos: boolean = connect || box || site;

    if (usaCalima && usaExternos) {
      let mensagem = 'Os usuário desse perfil terão acesso ao calima e ao ';

      if (connect) {
        mensagem += '/Calima Connect';
      }
      if (box) {
        mensagem += '/Calima Box';
      }
      if (site) {
        mensagem += '/Calima Site';
      }
      mensagem += '.\nConfirma a gravação?';

      const params: AlertProps = {
        message: mensagem,
        onOkClick: () => {
          done(formData);
        },
        type: AlertType.WARNING_YES_NO,
      };

      alertManager.emit(ACTION_EVENTS.SHOW, params);
    }
    return { async: true };
  };

  return (
    <WindowComponent
      title="Configuração dos Perfis de Usuários"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      wikiId={1558}
    >
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME} afterPersist={afterPersist} beforePersist={beforePersist}>
        <InputGroupInline formProps={formProps} hidden={true} id="perfilModulo.id" />
        <Row>
          <Col>
            <InputGroupInline
              id="descricao"
              inputProps={true}
              label="Descrição"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              placeholder="Informe a Descrição"
            />
          </Col>
        </Row>
        <FormGroup tag={Row}>
          <Col>
            <CustomInput
              type="switch"
              id="idvisualizarApenasRelatorios"
              name="visualizarApenasRelatorios"
              label="Visualizar apenas relatórios"
              innerRef={formProps.register}
            />
            {withTooltip(propToolTip('Permitir a visualização somente de relatórios.'), 'visualizarApenasRelatorios')}
          </Col>
          <Col>
            <CustomInput
              type="switch"
              id="idacessoFinanceiro"
              name="acessoFinanceiro"
              label="Acesso ao Financeiro"
              innerRef={formProps.register}
              disabled={podeAlterarAcessoFinanceiro}
            />
            {withTooltip(propToolTip('Permitir o acesso ao financeiro.'), 'acessoFinanceiro')}
          </Col>
        </FormGroup>
        <FormGroup tag={Row}>
          <Col>
            <CustomInput
              type="switch"
              id="idpermitirRestauracaoPeriodoFechado"
              name="permitirRestauracaoPeriodoFechado"
              label="Restauração de Períodos Fechados"
              innerRef={formProps.register}
            />
            {withTooltip(propToolTip('Permitir a restauração de períodos fechados.'), 'permitirRestauracaoPeriodoFechado')}
          </Col>
        </FormGroup>
        <Card style={{ marginBottom: '16px', borderWidth: 'thin' }}>
          <CardBody>
            <CardTitle>Módulos</CardTitle>
            <FormGroup tag={Row}>
              <Col>
                <CustomInput type="switch" id="idperfilModuloContabil" name="perfilModulo.contabil" label="Contábil" innerRef={formProps.register} />
                {withTooltip(propToolTip('Permitir o acesso ao módulo Contábil.'), 'perfilModuloContabil')}
              </Col>
              <Col>
                <CustomInput type="switch" id="idperfilModuloFiscal" name="perfilModulo.fiscal" label="Fiscal" innerRef={formProps.register} />
                {withTooltip(propToolTip('Permitir o acesso ao módulo Fiscal.'), 'perfilModuloFiscal')}
              </Col>
              <Col>
                <CustomInput
                  type="switch"
                  id="idperfilModuloConnect"
                  name="perfilModulo.connect"
                  label="Calima Connect"
                  innerRef={formProps.register}
                  disabled={possuiConnect}
                />
                {withTooltip(
                  propToolTip(
                    'Permitir o acesso ao Calima Connect. Caso essa opção seja marcada, o usuário poderá acessar pelo site ou pelo calima.'
                  ),
                  'perfilModuloConnect'
                )}
              </Col>
            </FormGroup>

            <FormGroup tag={Row}>
              <Col>
                <CustomInput
                  type="switch"
                  id="idcontrolePatrimonial"
                  name="perfilModulo.controlePatrimonial"
                  label="Controle Patrimonial"
                  innerRef={formProps.register}
                />
                {withTooltip(propToolTip('Permitir o acesso ao módulo Controle Patrimonial.'), 'controlePatrimonial')}
              </Col>
              <Col>
                <CustomInput
                  type="switch"
                  id="idfolhaPagamento"
                  name="perfilModulo.folhaPagamento"
                  label="Folha Pagamento"
                  innerRef={formProps.register}
                />
                {withTooltip(propToolTip('Permitir o acesso ao módulo Folha Pagamento.'), 'folhaPagamento')}
              </Col>
              <Col>
                <CustomInput type="switch" id="idbox" name="perfilModulo.box" label="Calima Box" innerRef={formProps.register} disabled={possuiBox} />
                {withTooltip(
                  propToolTip(
                    'Permitir o acesso ao Calima Box. Caso essa opção seja marcada, o usuário poderá acessar pelo aplicativo ou pelo calima.'
                  ),
                  'box'
                )}
              </Col>
            </FormGroup>

            <FormGroup tag={Row}>
              <Col>
                <CustomInput type="switch" id="idhonorario" name="perfilModulo.honorario" label="Honorários" innerRef={formProps.register} />
                {withTooltip(propToolTip('Permitir o acesso ao módulo Honorários.'), 'honorario')}
              </Col>
              <Col>
                <CustomInput type="switch" id="idprotocolo" name="perfilModulo.protocolo" label="Protocolo" innerRef={formProps.register} />
                {withTooltip(propToolTip('Permitir o acesso ao módulo Protocolo.'), 'protocolo')}
              </Col>
              <Col>
                <CustomInput
                  type="switch"
                  id="idsite"
                  name="perfilModulo.site"
                  label="Calima Site"
                  innerRef={formProps.register}
                  disabled={possuiSite}
                />
                {withTooltip(propToolTip('Permitir o acesso ao Calima Site.'), 'site')}
              </Col>
            </FormGroup>
          </CardBody>
        </Card>
      </EditForm>
    </WindowComponent>
  );
};

export default PerfilEditView;
